body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.wrapper {
    /* padding-top: 50px; */
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 50px 10%;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
}
.nav-pills {
    position: fixed;
    top: 0;
    left: 0;
    background: #000;
    min-height: 65px;
    width: 100%;
    text-align: center;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: 2em;
    font-weight: bold;
    z-index: 99;
    white-space: nowrap;
}

.nav-pills a {
    color: #fff;
}

.nav-pills a:hover {
    color: #fff;
    background-color: #5133a4 !important;
}

.desc-normal {
    color: #fff;
}

.desc-correct {
    color: #0f0;
}

.desc-incorrect {
    color: #f00;
}
.titlebox {
    background-image: url('/img/dogs_bg_denim.jpg');
    background-size: 100%;
    background-repeat: no-repeat;
    width: 100%;
    min-height: 280px;
    text-align: center;
    vertical-align: center;
    /* opacity: 0.3; */
    /* filter: alpha(opacity=30); */
    font-family: 'Holtwood One SC', serif;
    color: #fff;
    text-shadow: 0 0 5px #000;
    margin-bottom: 20px;
}

.titlebox h1 {
    margin-top: 90px;
    font-size: 3.5em;
}

.titlebox p {
    font-size: 1.8em;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 160px;
    overflow: hidden;
    margin: 1rem;
    position: relative;
    width: 250px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
}

.card > .img-container {
    width: 100%;
    height: auto;
    overflow: hidden;
    text-align: center;
}
.card > .img-container img {
    margin-top: 10px;
    width: 100%;
    height: 140px;
}

.style_prevu_kit
{
    display:inline-block;
    border:0;
    width:200px;
    height:220px;
    position: relative;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1); 
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1); 
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1);
    transition: all 200ms ease-in;
    transform: scale(1);   
}

.style_prevu_kit:hover
{
    cursor:pointer;
    box-shadow: 0px 0px 150px #000000;
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1.3);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.3);   
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.3);
    transition: all 200ms ease-in;
    transform: scale(1.3);
}

/* .style_prevu_kit:active {
    animation: shake 0.5s;
    animation-iteration-count: 1;
} */

.style_prevu_kit_ex {
    -webkit-animation: shake 0.3s;
            animation: shake 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}

@keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}

